body {
  outline: 1px solid lightgrey;
  margin: 0;
}
.container{
  max-width: 1200px;
  margin: 0 auto;
}
.header_container{
  width: 100%;
  background-color: rgba($color: #676566, $alpha: 0.6);
  position: absolute;
  top: 0;
  z-index: 1;
}
.logo_nav{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  
}
.header_logo{
  font-family: 'Scriptina Regular';
  font-size: 30px;
  color: #fff;
  letter-spacing: 10px;
}
.nav{
  padding: 5px 0;
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  font-family: 'Alegreya Regular';
  letter-spacing: 3px;
  word-spacing: 30px;
}
.nav:nth-child(2n){
  padding: 0;
}
.nav_icons{
  padding: 10px 0 10px 0;
  margin-right: 20px;
}
.nav_icons:nth-child(3){
  margin-right: 0;
}
.intro{
  width: 100%;
  height: 100vh;
  background-image: url('./img/introbread.jpg');
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.dot_shadow{
 width: 100%;
 height: 100%;
 background-image: radial-gradient(circle 457px at center, #564f4c 0%, rgba(86, 83, 76, 0) 100%);
 opacity: 0.9;
 position: absolute;
}
.intro_content{
  z-index: 1;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}
.intro_line{
  font-family: 'Playfair Display';
  font-size: 92px;
  font-weight: bold;
  color: #fff;
  letter-spacing:10px;
}
.intro_img{
  margin-top: 56px;
}
.intro_line_line{
  max-width: 700px;
  font-family: 'Proxima Nova Regular';
  font-size: 24px;
  font-weight: 400;
  color: #fff;
  letter-spacing: 5px;
  line-height: 36px;
  margin-top: 69px;
}
.our_menu{
  margin-top: 69px;
  padding: 20px 50px;
  border: none;
  color: #ffffff;
  font-family: 'Alegreya';
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 4.2px;
  background-color: rgba($color: #676566, $alpha: 0.6);
  z-index: 1;
}
.border_line{
 height: 10px;
}
.border_sphere{

  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 95%;
  left: 50%;
  margin-left: -50px;
}
.second_page{
  width: 100%;
  height: 100%;
  background-image: url('./img/page2background.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.small_bread{
  max-width: 50px;
  max-height: 45px;
  position: absolute;
  top:104%;
  left: 64%;
  
}
.small_container_bread{
  height: 100px;
}
.shef_page{
  font-family: 'Alegreya';
  display: flex;
  align-items: center;
}
.shef_content{
  display: inline-block;
  width: 47%;
  text-align: center;
}
.art_line{
  font-size: 48px;
  font-weight: bold;
  color: #56534c;
}
.create_line{
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  color: #a47d5e;
  letter-spacing: 1px;
}
.info_line{
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 70px;
  font-family: 'Proxima Nova Regular';
  font-size: 14px;
  font-weight: 600;
  color: #56534c;
  max-width: 490px;
  text-align: center;
  line-height: 24px;
  letter-spacing: 1.4px;
}
.chef_cook{
  margin-top: 45px;
  font-weight: bold;
  font-size: 24px;
  color: #56534c;
}
.shef_line{
  margin-top: 49px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-inline-start: auto;
  margin-inline-end: auto;
  max-width: 350px;
  width: 100%;
}
.benito{
  font-size: 24px;
  color: #56534c;
  font-weight: 500;
  padding: 20px 20px;
  border-bottom: 1px solid #d7d5ca;
  border-top: 1px solid #d7d5ca;
}
.unique_line{
margin-top: 45px;
max-width: 400px;
width: 100%;
height: 100px;
position: relative;
display: inline-block;
}
.first_simbol,.second_simbol{
  font-size: 60px;
  font-family: 'Times New Roman';
  color: #56534c;
  opacity: 0.5;
}
.first_simbol{
  position: absolute;
transform: rotate(-180deg);
}
.second_simbol{
  position: absolute;
  top:100%;
  left: 96%;
  margin-top: -34px;
  margin-left: -15px;
transform: rotateX(-180deg);
}
.text_unique{
  position: absolute;
  margin-top: -30px;
  margin-left: -115px;
  top: 50%;
  left: 50%;
  font-family: 'Scriptina';
  color: #a47d5e;
  font-size: 24px;
  line-height: 30px;
  max-width: 230px;
  text-align: center;
}
.tastes_images{
  width: 53%;
  position: relative;
}
.img_tastes_so{
  max-width: 247px;
  max-height: 247px;
 position: absolute;
 top:53%;
 left: 50%; 
 opacity: 0.9;
 margin-top: -123.5px;
 margin-left: -123.5px;
}
.bg_layer,.bg_layer2,.bg_layer3,.bg_layer4{
  max-width: 295px;
  max-height: 250px;
  width: 100%;
  height: 100%;
}
.bg_layer,.bg_layer2,.bg_layer3,.bg_layer4{
  padding: 10px 10px 10px 10px ;
}
